@keyframes hide{
  0% { transform: translate(0);}
  100% { transform: translate(-1020px, 1020px);}
}

@keyframes show {
  0% { transform: translate(-1020px, 1020px);}
  100% { transform: translate(0);}
}

.hide {
  animation: hide 0.5s forwards;
  /* animation-delay: .2s; */
}

.show {
  animation: show 0.5s forwards;
  animation-delay: 0s;
}


